*,
*::before,
*::after {
  box-sizing: inherit;
}

:root {
  box-sizing: border-box;
}

body {
  font-family: "Marianne", arial, sans-serif;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  margin: 0;
  padding: 0;
  font-size:  1rem ;
  line-height:  1.5rem ;
}

button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-color: transparent;
  font-family: inherit;
  border: none;
  margin: 0;
}

h6 {
  font-weight:  700 ;
  font-size:  1.125rem ;
  line-height:  1.5rem ;
  margin: var(--title-spacing);
}

@media (min-width: 48em) {
  h6 {
    font-size:  1.25rem ;
    line-height:  1.75rem ;
  }
}

h5 {
  font-weight:  700 ;
  font-size:  1.25rem ;
  line-height:  1.75rem ;
  margin: var(--title-spacing);
}

@media (min-width: 48em) {
  h5 {
    font-size:  1.375rem ;
    line-height:  1.75rem ;
  }
}

h4 {
  font-weight:  700 ;
  font-size:  1.375rem ;
  line-height:  1.75rem ;
  margin: var(--title-spacing);
}

@media (min-width: 48em) {
  h4 {
    font-size:  1.5rem ;
    line-height:  2rem ;
  }
}

h3 {
  font-weight:  700 ;
  font-size:  1.5rem ;
  line-height:  2rem ;
  margin: var(--title-spacing);
}

@media (min-width: 48em) {
  h3 {
    font-size:  1.75rem ;
    line-height:  2.25rem ;
  }
}

h2 {
  font-weight:  700 ;
  font-size:  1.75rem ;
  line-height:  2.25rem ;
  margin: var(--title-spacing);
}

@media (min-width: 48em) {
  h2 {
    font-size:  2rem ;
    line-height:  2.5rem ;
  }
}

h1 {
  font-weight:  700 ;
  font-size:  2rem ;
  line-height:  2.5rem ;
  margin: var(--title-spacing);
}

@media (min-width: 48em) {
  h1 {
    font-size:  2.5rem ;
    line-height:  3rem ;
  }
}

p {
  font-size:  1rem ;
  line-height:  1.5rem ;
  margin: var(--text-spacing);
}

:root {
  --link-underline: 0 1px 0 0 currentColor;
  --link-blank-font: normal normal normal 1rem/1 dsfr-icon;
  --link-blank-content: "\00a0";
}

a {
  text-decoration: none;
  color: inherit;
}

[href] {
  box-shadow: var(--link-underline);
}

[target="_blank"]::after {
  content: var(--link-blank-content);
  font: var(--link-blank-font);
}

.fr-raw-link {
  --link-underline: none;
  --link-blank-font: none;
  --link-blank-content: none;
}

.fr-reset-link {
  --link-underline: none;
  --link-blank-font: none;
  --link-blank-content: none;
  text-decoration: underline;
  text-underline-offset: 2px;
}

:root {
  --ul-type: "●\00a0\00a0";
  --ol-type: decimal;
  --ul-start: 1rem;
  --ol-start: 1.5rem;
  --xl-block: 0.5rem;
  --li-bottom: 0.25rem;
  --xl-base: 1em;
  --ol-content: counters(li-counter, ".") ".\00a0\00a0";
}

ul,
ol {
  margin: 0;
  padding: 0;
  margin-block-start: var(--xl-block);
  margin-block-end: var(--xl-block);
  --xl-size: var(--xl-base);
}

ul {
  list-style-type: var(--ul-type);
  padding-inline-start: var(--ul-start);
}

ul > li::marker {
  font-size: calc(var(--xl-size) * 0.9);
}

ol {
  list-style-type: var(--ol-type);
  padding-inline-start: var(--ol-start);
  counter-reset: li-counter;
}

ol > li {
  counter-increment: li-counter;
}

ol > li::marker {
  content: var(--ol-content);
  font-size: var(--xl-size);
  font-weight: bold;
}

li {
  --xl-base: calc(var(--xl-size) * 0.9);
  padding-bottom: var(--li-bottom);
}

.fr-raw-list {
  --ul-type: none;
  --ol-type: none;
  --ul-start: 0;
  --ol-start: 0;
  --xl-block: 0;
  --li-bottom: 0;
  --ol-content: none;
}

.fr-list {
  --ul-type: "●\00a0\00a0";
  --ol-type: decimal;
  --ul-start: 1rem;
  --ol-start: 1.5rem;
  --xl-block: 0.5rem;
  --li-bottom: 0.25rem;
  --xl-base: 1em;
  --ol-content: counters(li-counter, ".") ".\00a0\00a0";
}

hr {
  padding: var(--text-spacing);
  margin: 0;
  border: 0;
  display: block;
  background-size: 100% 1px;
  background-repeat: no-repeat;
  background-position: 0 0;
}

input,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  font-family: "Marianne", arial, sans-serif;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  border-radius: 0;
  border: 0;
  background-color: transparent;
  margin: 0;
}

