  --background-overlay: hsla(0deg, 0%, 100%, 0.375);
}

/* COMPONENT MAIN */
/* ------------------------------------ *\
  UPLOAD MAIN
\* ------------------------------------ */
.fr-upload-group .fr-label .fr-hint-text {
  margin-top: 0.75rem;
}

.fr-upload-group .fr-label + .fr-upload {
  margin-top: 1rem;
}

.fr-upload {
  font-size:  0.875rem ;
  line-height:  1.5rem ;
  width: 100%;
  display: inline-flex;
  margin: 0;
}

.fr-upload:disabled::-webkit-file-upload-button {
  outline-style: none;
  cursor: not-allowed;
}

.fr-upload::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
  cursor: pointer;
  margin-right: 0.5rem;
}

/* ------------------------------------ *\
  LOGO
\* ------------------------------------ */
.fr-logo {
  --link-underline: none;
  --link-blank-font: none;
  --link-blank-content: none;
  display: inline-block;
  padding: 1em;
  font-weight: 700;
  line-height: 1.03175em;
  letter-spacing: -0.01em;
  text-transform: uppercase;
  vertical-align: middle;
  text-indent: -0.1em;
  font-size: 1.05rem;
  margin-bottom: 0;
}

.fr-logo::before {
  width: 2.75rem;
  height: 1rem;
  margin-bottom: 0.33333rem;
  background-size: 2.75rem 1.125rem, 2.75rem 1rem, 0;
  background-position: 0 -0.0625rem, 0 0, 0 0;
}

:root[data-fr-mourning] .fr-logo::before {
  background-size: 2.75rem 1.125rem, 0, 2.75rem 1rem;
}

.fr-logo::after {
  min-width: 2.625rem;
  background-size: 5.25rem 3.75rem;
  background-position: 0 calc(100% + 1.875rem);
  padding-top: 2.20833rem;
}

[data-fr-theme="dark"] .fr-logo::after {
  background-position: -2.625rem 100%;
}

