.fr-logo--sm {
  font-size: 0.7875rem;
}

.fr-logo--sm::before {
  width: 2.0625rem;
  height: 0.75rem;
  margin-bottom: 0.25rem;
  background-size: 2.0625rem 0.84375rem, 2.0625rem 0.75rem, 0;
  background-position: 0 -0.04688rem, 0 0, 0 0;
}

:root[data-fr-mourning] .fr-logo--sm::before {
  background-size: 2.0625rem 0.84375rem, 0, 2.0625rem 0.75rem;
}

.fr-logo--sm::after {
  min-width: 1.96875rem;
  background-size: 3.9375rem 2.8125rem;
  background-position: 0 calc(100% + 1.40625rem);
  padding-top: 1.65625rem;
}

[data-fr-theme="dark"] .fr-logo--sm::after {
  background-position: -1.96875rem 100%;
}

.fr-logo--lg {
  font-size: 1.3125rem;
}

.fr-logo--lg::before {
  width: 3.4375rem;
  height: 1.25rem;
  margin-bottom: 0.41667rem;
  background-size: 3.4375rem 1.40625rem, 3.4375rem 1.25rem, 0;
  background-position: 0 -0.07812rem, 0 0, 0 0;
}

:root[data-fr-mourning] .fr-logo--lg::before {
  background-size: 3.4375rem 1.40625rem, 0, 3.4375rem 1.25rem;
}

.fr-logo--lg::after {
  min-width: 3.28125rem;
  background-size: 6.5625rem 4.6875rem;
  background-position: 0 calc(100% + 2.34375rem);
  padding-top: 2.76042rem;
}

[data-fr-theme="dark"] .fr-logo--lg::after {
  background-position: -3.28125rem 100%;
}

:root[data-fr-mourning] .fr-logo::before {
  box-shadow: 0 0 0 1px #fff;
}

.fr-logo {
  color: var(--text-title-grey);
}

/* ------------------------------------ *\
  CARD
\* ------------------------------------ */
.fr-card {
  display: flex;
  flex-direction: column-reverse;
  position: relative;
  --text-spacing: 0 0 1rem;
  --title-spacing: 0 0 0.5rem;
}

.fr-grid-row .fr-card {
  height: 100%;
}

.fr-card:not(.fr-card--no-arrow) .fr-card__body::after {
  font: normal normal normal 1.5rem/1 dsfr-icon;
  content: "";
  width: 1.5rem;
  height: 1.5rem;
  margin-top: auto;
  margin-left: auto;
}

.fr-card__img {
  position: relative;
  width: 100%;
}

.fr-card__img::before {
  content: "";
  display: block;
  width: 100%;
  padding-bottom: 56.25%;
}

.fr-card__img img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: 50% 50%;
}

.fr-card__body {
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  padding: 1.5rem;
}

.fr-card__detail {
  font-size:  0.75rem ;
  line-height:  1.25rem ;
  order: -1;
}

.fr-card__title {
  font-weight:  700 ;
  font-size:  1.125rem ;
  line-height:  1.5rem ;
  font-weight: 700;
}

@media (min-width: 48em) {
  .fr-card__title {
    font-size:  1.25rem ;
    line-height:  1.75rem ;
  }
}

.fr-card__desc {
  font-size:  0.875rem ;
  line-height:  1.5rem ;
}

.fr-card__link {
  --link-underline: none;
  --link-blank-font: none;
  --link-blank-content: none;
  display: inline-flex;
  width: 100%;
  margin-top: auto;
}

@media (min-width: 48em) {
  .fr-card--horizontal {
    flex-direction: row-reverse;
  }
  .fr-card--horizontal .fr-card__img {
    max-width: 41.666%;
    flex: 1 0 auto;
  }
}

.fr-card {
  --blend: var(--background-default-grey-blend);
  background-color: var(--background-default-grey);
  box-shadow: inset 0 0 0 1px var(--border-default-grey);
}

.fr-card--grey {
  --blend: var(--background-contrast-grey-blend);
  background-color: var(--background-contrast-grey);
}

.fr-card:not(.fr-card--no-arrow) .fr-card__body::after {
  color: var(--text-action-high-blue-france);
}

.fr-card__detail {
  color: var(--text-mention-grey);
}

.fr-card__title {
  color: var(--text-title-grey);
}

/* ------------------------------------ *\
  QUOTE
\* ------------------------------------ */
.fr-quote {
  position: relative;
  margin: 0;
  padding: 0 0.5rem 2rem 0.5rem;
  background-size: 50% 1px;
  background-repeat: no-repeat;
  background-position: 0.5rem 100%;
}

@media (min-width: 48em) {
  .fr-quote {
    padding: 0 0 0 2rem;
  }
}

@media (min-width: 48em) {
  .fr-quote {
    background-size: 1px 100%;
    background-position: 0 0;
  }
}

.fr-quote::before {
  font: normal normal normal 2rem/1 dsfr-icon;
  content: "";
  display: block;
  padding-bottom: 0.5rem;
}

.fr-quote--column {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

@media (min-width: 48em) {
  .fr-quote--column {
    margin-left: 13rem;
  }
}

@media (min-width: 48em) {
  .fr-quote--column {
    min-height: 13rem;
  }
}

.fr-quote--column .fr-quote__author {
  margin-left: 5.25rem;
}

@media (min-width: 48em) {
  .fr-quote--column .fr-quote__author {
    margin-left: 0;
  }
}

.fr-quote--column .fr-quote__source {
  margin-left: 5.25rem;
}

@media (min-width: 48em) {
  .fr-quote--column .fr-quote__source {
    margin-left: 0;
  }
}

.fr-quote--column figcaption {
  min-height: 4.25rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

@media (min-width: 48em) {
  .fr-quote--column figcaption {
    min-height: auto;
  }
}

.fr-quote blockquote {
  margin: 0;
  --text-spacing: 0 0 1rem;
}

.fr-quote blockquote p {
  font-size:  1.25rem ;
  line-height:  2rem ;
  font-weight: 700;
}

@media (min-width: 48em) {
  .fr-quote blockquote p {
    font-size:  1.375rem ;
    line-height:  2.25rem ;
  }
}

.fr-quote__author {
  font-weight: 700;
  font-size:  0.875rem ;
  line-height:  1.5rem ;
  margin-bottom: 0.25rem;
}

.fr-quote__image {
  width: 4.25rem;
  background-size: cover;
  border-radius: 50%;
  overflow: hidden;
  position: absolute;
  display: flex;
  flex-direction: row;
}

.fr-quote__image img {
  width: 100%;
  height: 100%;
}

@media (min-width: 48em) {
  .fr-quote__image {
    position: absolute;
    top: 50%;
    left: -13rem;
    width: 11.5rem;
    height: 11.5rem;
    margin-top: -5.75rem;
  }
}

.fr-quote cite {
  font-size:  0.75rem ;
  line-height:  1.25rem ;
  font-style: italic;
}

.fr-quote figcaption {
  --ul-type: none;
  --ol-type: none;
  --ul-start: 0;
  --ol-start: 0;
  --xl-block: 0;
  --li-bottom: 0;
  --ol-content: none;
}

.fr-quote figcaption li {
  font-size:  0.75rem ;
  line-height:  1.25rem ;
}

.fr-quote figcaption .fr-quote__source {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.fr-quote figcaption .fr-quote__source li {
  display: inline-block;
}

.fr-quote figcaption .fr-quote__source li:not(:last-child)::after {
  content: "·";
  display: inline-block;
  padding-left: 0.25rem;
  padding-right: 0.25rem;
}

.fr-quote {
  background-image: linear-gradient(0deg, var(--border-default-grey), var(--border-default-grey));
}

.fr-quote::before {
  color: var(--artwork-minor-blue-france);
}

.fr-quote--green-tilleul-verveine::before {
  color: var(--artwork-minor-green-tilleul-verveine);
}

.fr-quote--green-bourgeon::before {
  color: var(--artwork-minor-green-bourgeon);
}

.fr-quote--green-emeraude::before {
  color: var(--artwork-minor-green-emeraude);
}

.fr-quote--green-menthe::before {
  color: var(--artwork-minor-green-menthe);
}

.fr-quote--green-archipel::before {
  color: var(--artwork-minor-green-archipel);
}

.fr-quote--blue-ecume::before {
  color: var(--artwork-minor-blue-ecume);
}

.fr-quote--blue-cumulus::before {
  color: var(--artwork-minor-blue-cumulus);
}

.fr-quote--purple-glycine::before {
  color: var(--artwork-minor-purple-glycine);
}

.fr-quote--pink-macaron::before {
  color: var(--artwork-minor-pink-macaron);
}

.fr-quote--pink-tuile::before {
  color: var(--artwork-minor-pink-tuile);
}

.fr-quote--yellow-tournesol::before {
  color: var(--artwork-minor-yellow-tournesol);
}

.fr-quote--yellow-moutarde::before {
  color: var(--artwork-minor-yellow-moutarde);
}

.fr-quote--orange-terre-battue::before {
  color: var(--artwork-minor-orange-terre-battue);
}

.fr-quote--brown-cafe-creme::before {
  color: var(--artwork-minor-brown-cafe-creme);
}

.fr-quote--brown-caramel::before {
  color: var(--artwork-minor-brown-caramel);
}

.fr-quote--brown-opera::before {
  color: var(--artwork-minor-brown-opera);
}

.fr-quote--beige-gris-galet::before {
  color: var(--artwork-minor-beige-gris-galet);
}

/* ------------------------------------ *\
  BREADCRUMB
\* ------------------------------------ */
/**
un padding de 4px et une marge négative en compensation sont mis en place afin d'éviter de couper le focus.
 */
.fr-breadcrumb {
  margin: 1rem 0 2rem;
  font-size:  0.75rem ;
  line-height:  1.25rem ;
  --text-spacing: 0 0 0.5rem;
  position: relative;
  /**
   * margin-bottom de 2v pour créer cet espace entre chaque ligne
   */
}

@media (min-width: 48em) {
  .fr-breadcrumb {
    margin-bottom: 2.5rem;
  }
}

.fr-breadcrumb__button {
  box-shadow: var(--link-underline);
  font-size:  0.75rem ;
  line-height:  1.25rem ;
  margin: 0;
  padding: 0;
  color: inherit;
}

.fr-breadcrumb__button[aria-expanded="true"] {
  display: none;
}

@media (min-width: 48em) {
  .fr-breadcrumb__button {
    display: none;
  }
}

.fr-breadcrumb .fr-collapse {
  margin-left: -4px;
  margin-right: -4px;
  padding-left: 4px;
  padding-right: 4px;
  transform: translateY(-4px);
}

@media (min-width: 48em) {
  .fr-breadcrumb .fr-collapse {
    margin-left: 0;
    margin-right: 0;
    padding-left: 0;
    padding-right: 0;
    transform: none;
    visibility: inherit;
    overflow: visible;
    max-height: initial;
  }
  .fr-breadcrumb .fr-collapse::before {
    content: none;
  }
}

.fr-breadcrumb__list {
  --ul-type: none;
  --ol-type: none;
  --ul-start: 0;
  --ol-start: 0;
  --xl-block: 0;
  --li-bottom: 0;
  --ol-content: none;
